import { Component, OnInit, Input, Output, EventEmitter, TemplateRef, Optional } from '@angular/core';
import { tabListItem, ErrorListsItem, validatorListItem } from './verify-detail.interface';
import { LocaleService } from '@farris/ui-locale';
import { DomSanitizer } from '@angular/platform-browser';


@Component({
  selector: 'farris-verify-detail',
  templateUrl: './verify-detail.component.html',
  styleUrls: ['./verify-detail.component.scss']
})
export class VerifyDetailComponent implements OnInit {
  // è¿½å ç»ææç¤ºæ¨¡æ¿
  @Input() resultTipsTmpl: TemplateRef<any>=null;
  //@Input() maxHeight:number;
  //@Input() validatorList;
  //è¡¨åéªè¯åç»ä¿¡æ¯
  //@Input() tabList:tabListItem[];
  //è¡¨åéªè¯é»è®¤æ¾ç¤ºçåç»ä¿¡æ¯
  @Input() showType: string = '';
  //æ¯å¦é»è®¤æ¾ç¤ºéªè¯ä¿¡æ¯åè¡¨
  @Input() showList: boolean = false
  //éªè¯ä¿¡æ¯ç¹å»
  @Output('validatorClick') validatorClick = new EventEmitter();
  //éªè¯è¯¦ææ¾ç¤ºéèäºä»¶
  @Output('listshowChange') listshowChange = new EventEmitter();
  //showList = false;
  //å¤çåå¸¦åç»çè¡¨åéªè¯ä¿¡æ¯
  errorLists: ErrorListsItem[] = [];
  //é»è®¤ä¼ å¥çè¡¨åéªè¯ä¿¡æ¯æ°ç»
  _validatorList: validatorListItem[] = [];
  //åè¡¨é¨åæå¤§é«åº¦
  _maxHeight: number = 60;
  _tablist: tabListItem[];
  //æå¤§é«åº¦
  @Input()
  set maxHeight(num) {
    if (num < 50) return;
    this._maxHeight = num - 50;
  }
  get maxHeight() {
    return this._maxHeight;
  }

  @Input()
  set tabList(item) {
    if (item) {
      this._tablist = item;
    }
    else {
      const allTitle = this.localeService.getValue('verifyDetail.vertifyTypeAll'),
        errorTitle = this.localeService.getValue('verifyDetail.vertifyTypeError'),
        emptyTitle = this.localeService.getValue('verifyDetail.vertifyTypeEmpty');
      this._tablist = [
        {
          id: 'vertifyType1',
          type: 'all',
          title: allTitle
        },
        {
          id: 'vertifyType2',
          type: 'error',
          title: errorTitle
        },
        {
          id: 'vertifyType3',
          type: 'empty',
          title: emptyTitle
        }
      ]
    }

  }
  get tabList() {
    return this._tablist;
  }

  @Input()
  set validatorList(obj) {
    this._validatorList = obj;
    this.errorLists = [];
    if (this.tabList && this.tabList.length) {

      this.tabList.forEach(item => {
        item.active = false;
      })
      //å¤æ­é»è®¤å±ç¤ºçç±»å
      let hasTab = this.tabList.find((tab) => {
        return tab.type === this.showType;
      })
      if (this.showType === '' || !hasTab) {
        this.showType = this.tabList[0].type;
      }
      this.tabList.forEach(tab => {
        const tabType = tab.type;
        if (tabType === 'all') {
          // ææç±»å
          let listObj = {
            'list': obj,
            'show': false,
            'type': tabType
          };
          tab.length = listObj.list.length;
          this.errorLists.push(listObj);
        }
        else {
          let hasType = this.errorLists.find((item) => {
            return item.type === tabType;
          })
          if (hasType) {
            // è½æ¾å°ç±»å
            let valList = obj.filter(error => {
              return error.type === tabType;
            })
            if (valList && valList.length) {
              hasType.list = valList;
            }
          }
          else {
            // æ¾ä¸å°ç±»å
            let listObj = {
              'list': [],
              'show': false,
              'type': tabType
            }
            let valList = obj.filter(error => {
              return error.type === tabType;
            })
            if (valList && valList.length) {
              listObj.list = valList;
            }
            tab.length = listObj.list.length;
            this.errorLists.push(listObj);
          }
        }
      });
      let tabObj = this.tabList.find(item => {
        return item.type === this.showType;
      })
      if (tabObj) {
        tabObj.active = true;
      }
    }
    let showObj = this.errorLists.find(item => {
      return item.type === this.showType;
    })
    if (showObj) {
      showObj.show = true;
    }
  };
  get validatorList() {
    return this._validatorList;
  }

  constructor(public localeService: LocaleService,@Optional() private sanitizer: DomSanitizer) { }

  ngOnInit() { }
  //æ¾ç¤ºè¯¦æ
  show() {
    this.showList = !this.showList;
    this.listshowChange.emit(this.showList);
  }
  /**ç¹å»åç»åæ¢
   * @param tabåç»ä¿¡æ¯
   */
  showListContent(tab) {
    if (tab.length <= 0) {
      return;
    }
    this.tabList.forEach(item => {
      item.active = false;
    })
    tab.active = true;
    this.errorLists.forEach((item) => {
      if (item.type === tab.type) {
        item.show = true;
      }
      else {
        item.show = false;
      }
    })
  }
  /**ç¹å»å³é­æé® */
  close() {
    this.showList = false;
    this.listshowChange.emit(false);
  }
  /**ç¹å»è¡¨åéªè¯åè¡¨äºä»¶ */
  errorClick(item) {
    this.validatorClick.emit(item);
  }
  /**
   * æ ·å¼å®å¨å
   * @param typeInfo 
   */
  getStyle(typeInfo){
    if(this.sanitizer){
      if(typeInfo.hasOwnProperty('iconStyle')&&typeInfo['iconStyle']){
        return this.sanitizer.bypassSecurityTrustStyle(typeInfo['iconStyle']);
      }
    }    
    return null;
  }
  /**
   * å¤æ­æ¯å¦ææ°å±æ§
   */
  hasSelfDefind(typeInfo){
    if(typeInfo){
      if(typeInfo.hasOwnProperty('iconCls')&&typeInfo['iconCls']){
        return true;
      }
      if(typeInfo.hasOwnProperty('iconStyle')&&typeInfo['iconStyle']){
        return true;
      }
    }
    return false;
  }
}
